'use client';

import React, { useState, useEffect, useRef, KeyboardEvent } from 'react';
import { ChevronDown, ChevronRight, Menu, X, Sun, Moon, Home, BarChart3, Users, Settings, FileText, Package, ShoppingCart, CreditCard, Bell, Shield, Database, Cloud, Zap, Layers } from 'lucide-react';

interface MenuItem {
  id: string;
  label: string;
  icon: React.ElementType;
  activeIcon?: React.ElementType;
  href?: string;
  children?: MenuItem[];
}

const menuItems: MenuItem[] = [
  {
    id: 'dashboard',
    label: 'Dashboard',
    icon: Home,
    activeIcon: Home,
    href: '/dashboard'
  },
  {
    id: 'analytics',
    label: 'Analytics',
    icon: BarChart3,
    activeIcon: Zap,
    children: [
      {
        id: 'analytics-overview',
        label: 'Overview',
        icon: Layers,
        href: '/analytics/overview'
      },
      {
        id: 'analytics-reports',
        label: 'Reports',
        icon: FileText,
        children: [
          {
            id: 'analytics-reports-sales',
            label: 'Sales Reports',
            icon: ShoppingCart,
            href: '/analytics/reports/sales'
          },
          {
            id: 'analytics-reports-user',
            label: 'User Reports',
            icon: Users,
            href: '/analytics/reports/user'
          }
        ]
      }
    ]
  },
  {
    id: 'ecommerce',
    label: 'E-Commerce',
    icon: ShoppingCart,
    activeIcon: Package,
    children: [
      {
        id: 'ecommerce-products',
        label: 'Products',
        icon: Package,
        href: '/ecommerce/products'
      },
      {
        id: 'ecommerce-orders',
        label: 'Orders',
        icon: CreditCard,
        children: [
          {
            id: 'ecommerce-orders-pending',
            label: 'Pending',
            icon: Bell,
            href: '/ecommerce/orders/pending'
          },
          {
            id: 'ecommerce-orders-completed',
            label: 'Completed',
            icon: Shield,
            href: '/ecommerce/orders/completed'
          }
        ]
      }
    ]
  },
  {
    id: 'database',
    label: 'Database',
    icon: Database,
    activeIcon: Cloud,
    href: '/database'
  },
  {
    id: 'settings',
    label: 'Settings',
    icon: Settings,
    href: '/settings'
  }
];

export default function SidebarNavigation() {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [activeItem, setActiveItem] = useState<string>('dashboard');
  const [expandedItems, setExpandedItems] = useState<Set<string>>(new Set());
  const [isDarkMode, setIsDarkMode] = useState(false);
  const sidebarRef = useRef<HTMLDivElement>(null);

  // Initialize theme and collapsed state from localStorage
  useEffect(() => {
    const savedTheme = localStorage.getItem('theme');
    const savedCollapsed = localStorage.getItem('sidebarCollapsed');
    
    if (savedTheme === 'dark') {
      setIsDarkMode(true);
      document.documentElement.classList.add('dark');
    }
    
    if (savedCollapsed === 'true') {
      setIsCollapsed(true);
    }

    // Set initial CSS variables
    updateCSSVariables(savedTheme === 'dark');
  }, []);

  // Update CSS variables for theming
  const updateCSSVariables = (isDark: boolean) => {
    const root = document.documentElement;
    if (isDark) {
      root.style.setProperty('--bg-primary', '#000000');
      root.style.setProperty('--bg-secondary', '#5C4E4E');
      root.style.setProperty('--text-primary', '#FFFFFF');
      root.style.setProperty('--text-secondary', '#D1D0D0');
      root.style.setProperty('--accent', '#988686');
    } else {
      root.style.setProperty('--bg-primary', '#FFFFFF');
      root.style.setProperty('--bg-secondary', '#D1D0D0');
      root.style.setProperty('--text-primary', '#000000');
      root.style.setProperty('--text-secondary', '#5C4E4E');
      root.style.setProperty('--accent', '#988686');
    }
  };

  const toggleTheme = () => {
    const newTheme = !isDarkMode;
    setIsDarkMode(newTheme);
    localStorage.setItem('theme', newTheme ? 'dark' : 'light');
    document.documentElement.classList.toggle('dark');
    updateCSSVariables(newTheme);
  };

  const toggleSidebar = () => {
    const newCollapsed = !isCollapsed;
    setIsCollapsed(newCollapsed);
    localStorage.setItem('sidebarCollapsed', newCollapsed.toString());
  };

  const toggleExpanded = (itemId: string) => {
    setExpandedItems(prev => {
      const newSet = new Set(prev);
      if (newSet.has(itemId)) {
        newSet.delete(itemId);
      } else {
        newSet.add(itemId);
      }
      return newSet;
    });
  };

  const handleItemClick = (item: MenuItem) => {
    if (item.children) {
      toggleExpanded(item.id);
    } else if (item.href) {
      setActiveItem(item.id);
    }
  };

  const handleKeyDown = (e: KeyboardEvent, item: MenuItem) => {
    switch (e.key) {
      case 'Enter':
      case ' ':
        e.preventDefault();
        handleItemClick(item);
        break;
      case 'ArrowRight':
        if (item.children && !expandedItems.has(item.id)) {
          toggleExpanded(item.id);
        }
        break;
      case 'ArrowLeft':
        if (item.children && expandedItems.has(item.id)) {
          toggleExpanded(item.id);
        }
        break;
      case 'Escape':
        if (!isCollapsed) {
          toggleSidebar();
        }
        break;
    }
  };

  const renderMenuItem = (item: MenuItem, level: number = 0): JSX.Element => {
    const isActive = activeItem === item.id;
    const isExpanded = expandedItems.has(item.id);
    const hasChildren = item.children && item.children.length > 0;
    const Icon = isActive && item.activeIcon ? item.activeIcon : item.icon;

    return (
      <div key={item.id} className={`${level > 0 ? 'ml-4' : ''}`}>
        <button
          onClick={() => handleItemClick(item)}
          onKeyDown={(e) => handleKeyDown(e, item)}
          className={`
            w-full flex items-center gap-3 px-3 py-2.5 rounded-lg
            transition-all duration-200 ease-out
            ${isActive 
              ? 'bg-[var(--accent)] text-[var(--bg-primary)] shadow-md' 
              : 'text-[var(--text-secondary)] hover:bg-[var(--bg-secondary)] hover:text-[var(--text-primary)]'
            }
            ${isCollapsed && level === 0 ? 'justify-center' : ''}
            focus:outline-none focus:ring-2 focus:ring-[var(--accent)] focus:ring-opacity-50
          `}
          role="menuitem"
          aria-expanded={hasChildren ? isExpanded : undefined}
          aria-current={isActive ? 'page' : undefined}
        >
          <Icon 
            size={20} 
            className={`
              transition-transform duration-200
              ${isActive ? 'scale-110' : ''}
              ${isCollapsed && level === 0 ? 'mx-auto' : 'flex-shrink-0'}
            `}
          />
          
          {(!isCollapsed || level > 0) && (
            <>
              <span className="flex-1 text-left text-sm font-medium">
                {item.label}
              </span>
              
              {hasChildren && (
                <ChevronDown 
                  size={16} 
                  className={`
                    transition-transform duration-200 flex-shrink-0
                    ${isExpanded ? 'rotate-180' : ''}
                  `}
                />
              )}
            </>
          )}
        </button>

        {hasChildren && isExpanded && (!isCollapsed || level > 0) && (
          <div className="mt-1 space-y-0.5 animate-slideDown">
            {item.children!.map(child => renderMenuItem(child, level + 1))}
          </div>
        )}
      </div>
    );
  };

  return (
    <>
      <style jsx global>{`
        @keyframes slideDown {
          from {
            opacity: 0;
            transform: translateY(-10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        .animate-slideDown {
          animation: slideDown 0.2s ease-out;
        }

        :root {
          --bg-primary: #FFFFFF;
          --bg-secondary: #D1D0D0;
          --text-primary: #000000;
          --text-secondary: #5C4E4E;
          --accent: #988686;
        }
      `}</style>

      <div
        ref={sidebarRef}
        className={`
          fixed left-0 top-0 h-full bg-[var(--bg-primary)] 
          shadow-2xl transition-all duration-300 ease-out z-50
          ${isCollapsed ? 'w-20' : 'w-72'}
        `}
        role="navigation"
        aria-label="Main navigation"
      >
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-[var(--bg-secondary)]">
          <h1 
            className={`
              font-bold text-xl text-[var(--text-primary)] 
              transition-all duration-300
              ${isCollapsed ? 'opacity-0 w-0' : 'opacity-100'}
            `}
          >
            Dashboard
          </h1>
          
          <button
            onClick={toggleSidebar}
            className="p-2 rounded-lg hover:bg-[var(--bg-secondary)] transition-colors duration-200
                     focus:outline-none focus:ring-2 focus:ring-[var(--accent)] focus:ring-opacity-50"
            aria-label={isCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
          >
            {isCollapsed ? (
              <Menu size={20} className="text-[var(--text-primary)]" />
            ) : (
              <X size={20} className="text-[var(--text-primary)]" />
            )}
          </button>
        </div>

        {/* Theme Toggle */}
        <div className={`p-4 border-b border-[var(--bg-secondary)] ${isCollapsed ? 'px-2' : ''}`}>
          <button
            onClick={toggleTheme}
            className={`
              w-full flex items-center gap-3 px-3 py-2.5 rounded-lg
              bg-[var(--bg-secondary)] hover:bg-[var(--accent)] 
              transition-all duration-200 group
              focus:outline-none focus:ring-2 focus:ring-[var(--accent)] focus:ring-opacity-50
              ${isCollapsed ? 'justify-center' : ''}
            `}
            aria-label="Toggle theme"
          >
            {isDarkMode ? (
              <Sun size={20} className="text-[var(--text-primary)] group-hover:rotate-180 transition-transform duration-500" />
            ) : (
              <Moon size={20} className="text-[var(--text-primary)] group-hover:rotate-12 transition-transform duration-500" />
            )}
            {!isCollapsed && (
              <span className="text-sm font-medium text-[var(--text-primary)]">
                {isDarkMode ? 'Light Mode' : 'Dark Mode'}
              </span>
            )}
          </button>
        </div>

        {/* Menu Items */}
        <nav 
          className="p-4 space-y-1 overflow-y-auto h-[calc(100%-140px)]"
          role="menu"
        >
          {menuItems.map(item => renderMenuItem(item))}
        </nav>
      </div>

      {/* Main Content Offset */}
      <div className={`transition-all duration-300 ${isCollapsed ? 'ml-20' : 'ml-72'}`}>
        {/* Your main content goes here */}
      </div>
    </>
  );
}